                <br />—Hynek Schlawack
                <br />Python忠實支持者、部落客及演說者
                <br />
                <br />隨著Python越來越流行，專案的規模也越來越大、越來越複雜，讓許多Python開發者對高階軟體設計模式產生濃厚的興趣，例如六邊形/整潔架構、事件驅動架構，和以領域驅動設計(DDD)制定的策略模式，但是將這些模式轉換成Python不是那麼容易。
                <br />
                <br />透過這本實戰指南，來自MADE.com的Harry Percival與Bob Gregory介紹一些行之有效的架構設計模式，協助Python開發者管理app的複雜性，並且充分利用測試套件。
                <br />
