                <br />‧事件、指令和訊息匯流排
                <br />‧命令查詢責任隔離(CQRS)模式
                <br />‧事件驅動架構與反應式微服務
                <br />
                <br />Harry Percival 在2009年就開始使用Python了，他也是TDD和XP的粉絲。他是O’Reilly書籍《測試驅動開發｜使用Python》的作者，此書的副標題更是聞名—服從測試羊。
                <br />
